*{
    margin: 0px;
    padding: 0px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body{
    background-color: #fcdef8;
}


h1{
    text-align: center;
    margin-top: 10%;
    
}

.container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    
    
}


.container input{
   margin-top: 20px;
    width: 40%;
    border-radius: 5px;
outline: none;
padding: 7px;
   font-size: 15px;
}

button{
   position: relative;
   left: 42%;
   margin-top: 30px;
   padding: 8px;
   width: 20%;
   border-radius: 10px;
   background-color: white;
}